.card {
  display: flex;
  flex-direction: column;
}
.card-content {
  flex-grow: 1;
}
.basket-bottom {
  width: 70px;
  height: 70px;
  background-color: #26a69a;
  border-radius: 50%;
  padding: 20px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  color: white;
  z-index: 6;
  box-shadow:0 0 10px rgba(0,0,0,0.5);
}
.basket-bottom:hover{
  background-color: #5dd5cb;
  z-index: 3;
}

.windowBasket {
  /*width: 600px;*/
  /*height: auto;*/
  /*min-height: 300px;*/
  /*margin: 100px 100px;*/
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50rem;
  max-width: 95%;

  background-color: #26a69a;
  border-radius: 20px;

  z-index: 5;
  padding: 20px;
  box-shadow:0 0 10px rgba(0,0,0,0.5);
}
.closeBasket {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: white;
  color: black;
}
#formBasket {
  background-color: white;
  border-radius: 20px;
  position: relative;
  top: 0;
  padding: 10px;

}
h3 {
  margin-top: 0px;
  color: white;
}
.orderValue {
  z-index: 6;
  position: fixed;
  bottom: 35px;
  right: 28px;
  color: white;
}
.order {
  color: black;
  border-bottom: 1px #a1a1a1 solid;
  margin-bottom: 1rem;
  padding: 2px;
}

